import { TopicRequest } from './../Constants';
import HomeReducer, { homeState, HomeActionType } from './../Reducers';



test(TopicRequest, () => {
    expect(
        HomeReducer(homeState, {
            type: TopicRequest,
            payload: { page: 1, tab: 'all', limit: 20, mdrender: 'true' }
        } as HomeActionType)
    ).toEqual(homeState.set('isLoading', true))
})